/* ----------------
    Other Pages
------------------
================== */
.page-top-section {
    height: 478px;
    position: relative;
    padding-top: 220px;
    text-align: center;

    &.error {
        height: 105px;
        padding-top: 0;
    }

    &.page-sp {
        padding-top: 200px;
    }

    &:after {
        position: absolute;
        content: "";
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;

        @include opacity(.92);
    }

    .container {
        position: relative;
        z-index: 9;
    }

    h2 {
        font-size: 45px;
        text-transform: uppercase;
        margin-bottom: 20px;
    }

    p {
        font-size: 18px;
    }
}

.share-icon {
    display: none;
}

.ed-social .share-icon,
.cd-social .share-icon,
.bi-social .share-icon {
    display: inline-block;
}



